@border-color: #d1d1d1;
input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea,
select {
  // height: 3.8rem;
  font-size: @normal-size;
  width: 100%;
  padding: .6rem 1.0rem;
  // margin-top: 1rem;
  /* The .6rem vertically centers text on FF, ignored by Webkit */
  background-color: #fff;
  border: .1rem solid @border-color;
  border-radius: @radius;
  box-shadow: none;
  box-sizing: border-box;
  color: #444;
}

select:not([multiple]) {
  -webkit-appearance: none;
  background-position: right 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}


/* Removes awkward default styles on some inputs for iOS */

input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  min-height: 6.5rem;
  padding-top: .6rem;
  padding-bottom: .6rem;
}

input[type="email"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
  border: .1rem solid darken(@border-color, 10%);
  outline: 0;
}

label,
legend {
  display: block;
  margin-bottom: .5rem;
  font-size: 80%;
  font-weight: 600;
}

fieldset {
  padding: 0;
  border-width: 0;
}

input[type="checkbox"],
input[type="radio"] {
  display: inline;
}

label > .label-body {
  display: inline-block;
  margin-left: .5rem;
  font-weight: normal;
}

.form-group{
  margin-bottom: 1.5rem;
}
