  margin-top: 0;
}

// Lists
// ––––––––––––––––––––––––––––––––––––––––––––––––––
ul {
  list-style: circle inside;
}

ol {
  list-style: decimal inside;
}

ol,
ul {
  padding-left: 0;
  margin-top: 0;
}

ul ul,
ul ol,
ol ol,
ol ul {
  margin: 1.5rem 0 1.5rem 3rem;
  font-size: 90%;
}

li {
  margin-bottom: 1rem;
}

// Code
// ––––––––––––––––––––––––––––––––––––––––––––––––––
code {
  padding: .2rem .5rem;
  margin: 0 .2rem;
  font-size: 90%;
  white-space: nowrap;
  background: #F1F1F1;
  border: .1rem solid #E1E1E1;
  border-radius: .4rem;
}

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre;
}

// Table
// ––––––––––––––––––––––––––––––––––––––––––––––––––
table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1.225rem;
  // thead > tr,
  // tr:not(:last-child) {
  //   border-bottom: .1rem solid #E1E1E1;
  // }
  tr {
    border-bottom: .1rem solid #e1e1e1;
  }
  th,
  td {
    margin: 0;
    padding: 1rem;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  th:not(:last-child),
  td:not(:last-child) {
    // padding-left: 1rem;
    border-right: .1rem solid #e1e1e1;
  }
}

.close {
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 600;
  color: #f30;
}
